import React, { Component, PropTypes } from 'react';
import KlassName from 'teapan/klassName';
import { SIZES } from '../../constant/props';

const StringOrNumber = PropTypes.oneOfType([PropTypes.number, PropTypes.string]);

const itemProps = PropTypes.oneOfType([
	PropTypes.string,
	PropTypes.number,
	PropTypes.shape({
		size: StringOrNumber,
		push: StringOrNumber,
		pull: StringOrNumber,
		offset: StringOrNumber
	})
]);

class GridItem extends Component {
	constructor(props) {
		super(props);
	}

	getItemClassName() {
		let klassNames = [];

		SIZES.forEach((size) => {
			let itemProp = this.props[size];

			if(!itemProp) {
				return;
			} else if (itemProp.size) {
				klassNames.push(KlassName({
					[`grid-item--${size}--${itemProp.size}`]: itemProp.size,
					[`grid-item--${size}--push--${itemProp.push}`]: itemProp.push,
					[`grid-item--${size}--pull--${itemProp.pull}`]: itemProp.pull,
					[`grid-item--${size}--offset--${itemProp.offset}`]: itemProp.offset
				}));
			} else {
				klassNames.push(`grid-item--${size}--${itemProp}`);
			}
		});

		return klassNames;
	}

	render() {
		const props = this.props;

		const {
			className,
			children,
			...others
		} = props;

		const klassName = KlassName('grid-item',
			className,
			this.getItemClassName()
		);

		return (
			<div
				{...others}
				className={klassName}
			>
				{children}
			</div>
		);
	}
};

GridItem.propTypes = {
	tiny: itemProps,
	small: itemProps,
	medium: itemProps,
	large: itemProps,
	huge: itemProps,
	wide: itemProps,
};

GridItem.defaultProps = {
	tiny:'24',
};

export default GridItem;